import axios from "axios"
import {urlbase, getHeader} from "./../config/config"

export async function listaPedidos() {
    return await axios.get(`${urlbase}/pedido`, {headers: getHeader()});
}




export async function guardarPedido(datos) {
    return await axios.post(`${urlbase}/pedido`, datos, {headers: getHeader()})
}

export async function modificarPedido(id, datos) {
    return await axios.put(`${urlbase}/pedido/${id}`, datos, {headers: getHeader()})
}

export async function eliminarPedido(id) {
    return await axios.delete(`${urlbase}/pedido/${id}`, {headers: getHeader()})
}