<template>
  <v-container class="grey lighten-5">
    <v-row no-gutters>
      <v-col cols="12" sm="6" md="8">
        <v-card class="pa-2" outlined tile>
          <h1>Lista de Productos</h1>
          <v-data-table
            dense
            :headers="headers"
            :items="productos"
            item-key="name"
            class="elevation-1"
          >
            <template v-slot:[`item.acciones`]="{ item }">
              <v-btn @click="addCarrito(item)">seleccionar</v-btn>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
      <v-col cols="6" md="4">
        <v-card class="pa-2" outlined tile>
          <h1>Cliente</h1>
          <v-text-field
            v-model="buscar"
            label="Buscar"
            @keyup="buscarCliente()"
          ></v-text-field>

          <table border="1">
            <tr>
              <td>CI</td>
              <td>APELLIDOS</td>
              <td>NOMBRE</td>
              <td>ACCION</td>
            </tr>
            <tr v-for="(clie, index) in clientes" :key="index">
              <td>{{ clie.ci_nit }}</td>
              <td>{{ clie.nombres }}</td>
              <td>{{ clie.apellidos }}</td>
              <td>
                <v-btn @click="selecClie(clie)">-</v-btn>
              </td>
            </tr>
          </table>

          <h2>Carrito</h2>
          <table border="1">
            <tr>
              <td>NOMBRE</td>
              <td>PRECIO</td>
              <td>CANTIDAD</td>
              <td>ACCION</td>
            </tr>
            <tr v-for="(dato, index) in carrito" :key="index">
              <td>{{ dato.nombre }}</td>
              <td>{{ dato.precio }}</td>
              <td>1</td>
              <td>
                <v-btn @click="eliminarCarrito(index, dato)">x</v-btn>
              </td>
            </tr>
          </table>
          <h3>TOTAL: Bs. {{ total }}</h3>

          <v-btn @click="guardarPedido()">Guardar Pedido</v-btn>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { listaProducto } from "../../../services/productoService";
import { buscarCliente } from "../../../services/clienteService";
import { guardarPedido } from "../../../services/pedidoService";
export default {
  data() {
    return {
      carrito: [],
      productos: [],
      cliente_id: 0,
      headers: [
        {
          text: "Nombre",
          align: "start",
          sortable: false,
          value: "nombre",
        },
        { text: "Precio", value: "precio" },
        { text: "Cantidad", value: "cantidad" },
        { text: "categoria", value: "categoria_id" },
        { text: "Acciones", value: "acciones" },
      ],
      total: 0,
      buscar: "",
      clientes: [],
    };
  },
  async mounted() {
    try{
      let datos = await listaProducto();
      console.log(datos);
      this.productos = datos.data.data;

    }catch(error){
      localStorage.clear();
      this.$router.push("/login");
    }
  },
  methods: {
    addCarrito(item) {
      console.log(item);
      this.carrito.push(item);
      this.total += parseFloat(item.precio);
    },
    eliminarCarrito(index, dato) {
      this.carrito.splice(index, 1);
      this.total -= parseFloat(dato.precio);
    },
    async buscarCliente() {
      let { data } = await buscarCliente(this.buscar);
      console.log(data);
      this.clientes = data.data;
    },
    selecClie(clie){
      this.cliente_id = clie.id
    },
    async guardarPedido(){
      let prods_id= [];
      for (let i = 0; i < this.carrito.length; i++) {
        const prod = this.carrito[i];
          prods_id.push(prod.id);        
      }
      let pedido = {
        cliente_id: this.cliente_id,
        productos: prods_id
      }
      console.log(pedido);
      await guardarPedido(pedido);
      this.$router.push("/admin/pedido")
    }
  },
};
</script>

